import React,{useState, useEffect} from "react";
import { db } from "./firebase";

const Userflg = (props) => {
// emailuser_id
const email = props.location.state.email;
const [user, setUser] = useState([
    {
      id: "",
      user_flg:"",
    },
    ]);

       // firebasefirebase
       useEffect(() => {
        const firebaseData = db
            .collection("users")
            .where("email", "==", email)
            .onSnapshot((snapshot) =>
                setUser(
                  snapshot.docs.map((doc) => ({
                    // firebasedoc
                    // post
                    id: doc.id,
                    user_flg: doc.data().user_flg,
                  }))
                )
              );
        //   
        return () => {
            firebaseData();
            };
        }, []);    
    return (
        <> 
        {/* user_flg0 */}
        {user[0].user_flg === "0" && 
        props.history.push({
            pathname: "/guest",
            state: { myId : user[0].id,}
          })
        }

        {/* user_flg1 */}         
        {user[0].user_flg === "1" && 
        props.history.push({
            pathname: "/volunteer",
            state: { myId : user[0].id,}
          })
        }        
        </>
    )
}

export default Userflg;
