import React from 'react'
import Paper from '@material-ui/core/Paper';
import Img from "../../img/noimage.jpg";
import { makeStyles } from '@material-ui/core/styles';
import { Avatar, Button, ListItem, ListItemAvatar, ListItemText } from '@material-ui/core';
import { withRouter } from 'react-router-dom';
import "../../style.css";
import { db } from "../../firebase";
import firebase from "firebase/app";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        marginBottom: 8,
      },
      paper: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
      },
      large: {
        width: theme.spacing(8),
        height: theme.spacing(8),
      },
      avatar:{
        paddingRight:"10px",
      },
      list:{
        flexDirection: "column",
        padding: "0 0 0 0",
        alignItems: 'start',
      },
      image: {
        width: "5rem",
        height: "5rem",
      },
      img: {
        margin: 'auto',
        display: 'block',
        width: '100%',
        height: '100%',
        objectFit: "cover",
      },
      flex: {
        display: "flex",
      },
      button:{
          marginRight: 10,
      },
  }));

const SelectItem = ({id, picture, user_id, nickname, history, escortId, mynickname, rikkouho_id, myId, mypic}) => {
    const classes = useStyles();

    //
    const gotoVol=()=>{
         history.push({
            // id
            pathname: '/guest/volunteerprofile',
            state: { volunteerId: user_id,
                      myId: myId,
                      mynickname: mynickname,
                      mypic: mypic,
                      escortId: escortId,
                      rikkouho_id: rikkouho_id,}
        });
    }

    let volpic ="";
    if(!picture){
        volpic = "";
    }if(picture){
        volpic =picture;
    };  

    // 
    const selectVol=()=>{
        // dbescortsidstatus""status_2volunteer_iduser_idnickname
        db.collection("escorts").doc(escortId).update({
          status: "",
          status_2: "",
          volunteer_id: user_id,
          volunteer_pic: volpic,
          volunteer_nickname: nickname,
          rikkouho_id: "",
        });
      // notice
      db.collection("notices").add({
        escort_id: escortId,
        user_id: user_id,
        icon: 0,
        notice:`${mynickname}`,
        already_read: 0,
        created_at: firebase.firestore.FieldValue.serverTimestamp(),
      });
      // 
      //(1)rikkouho_idid(user_id)
      var index = rikkouho_id.indexOf(user_id);
      rikkouho_id.splice(index, 1)
      // (2)id 
      { rikkouho_id.lenghth !== 0 && rikkouho_id.map((item) => (
        db.collection("notices").add({
            escort_id: escortId,
            user_id: item,
            icon: 1,
            notice:`${mynickname}`,
            already_read: 0,
            created_at: firebase.firestore.FieldValue.serverTimestamp(),
          })
          ));
      }
    // 
    history.goBack()
   }


    return (
        <div key={id}>
            <div className={classes.root}>
            {/*  */}
            <Paper className={classes.paper}>
            <div className={classes.flex}>
                <ListItemAvatar className={classes.avatar}>
                    { picture ? (
                    <>
                        <Avatar variant="rounded" src={picture} alt={`${nickname}`}  className={classes.large}/>
                    </>
                    ) : (
                    <>
                        <Avatar variant="rounded" src={Img} alt=""  className={classes.large}/>
                    </>
                    )}
                </ListItemAvatar>
                <ListItem className={classes.list}>
                <ListItemText 
                primary={`${nickname}`}
                /> 
                <ListItemText
                    secondary={
                    <>
                    <Button variant="outlined" color="primary" size="small" className={classes.button} onClick={gotoVol}>
                        
                    </Button>
                    <Button variant="contained" color="primary" size="small" onClick={selectVol}>
                        
                    </Button>
                    </>
                    }/>         
                </ListItem>
            </div>
            </Paper>
            </div>            
        </div>
    )
}

export default withRouter(SelectItem)
