import React, {useState, useEffect} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import RequestItem from './RequestItem';
import { db } from "../../firebase";
import { Typography } from '@material-ui/core';

// 

// style
const useStyles = makeStyles((theme) => ({
  box: {
      paddingBottom: 10,
    },
    text:{
      margin: "0 auto",
      maxWidth: "500px",
      marginBottom: 10,
      textAlign: "center",
    },
}));

const WaitingReq = ({history, myId, mynickname, mypic}) => {
    const classes = useStyles();
    // escortsguest_idmyId
      // firebaseuseState
      const [request, setRequest] = useState([
          {
            id: "",
            guest_id:"",
            volunteer_id:"",
            rikkouho_id:"",
            es_date: "",
            start_time:"",
            departure: "",
            destination:"",
            number:"",
            lan_1:"",
            lan_2:"",
            lan_3:"",
            status: "",
            status_2: "",
            emergency: "",
          },
          ]);
  
      // firebasefirebase
      useEffect(() => {
        const firebaseData = db
            .collection("escorts")
            .where("guest_id", "==", myId)
            .where("status", "==", "")
            .orderBy("es_date", "asc")
            .onSnapshot((snapshot) =>
                setRequest(
                  snapshot.docs.map((doc) => ({
                    // firebasedoc
                    // 
                    id: doc.id,
                    guest_id: doc.data().guest_id,
                    rikkouho_id: doc.data().rikkouho_id,
                    es_date: doc.data().es_date,
                    start_time: doc.data().start_time,
                    departure: doc.data().departure,
                    destination: doc.data().destination,
                    number: doc.data().number,
                    lan_1: doc.data().lan_1,
                    lan_2: doc.data().lan_2,
                    lan_3: doc.data().lan_3,
                    status: doc.data().status,
                    status_2: doc.data().status_2,
                    emergency: doc.data().emergency,
                  }))
                )
              );
        //   
        return () => {
            firebaseData();
            };
        }, []);

    return (
        <div className={classes.box}>
                {/* request */}
                { request.length !== 0 && (
                <>
                {/* requestrequestItem */}
                {request.map((requestItem) => (
                    // requestItem
                    <RequestItem
                    key={requestItem.id}
                    id={requestItem.id}
                    picture={requestItem.picture}
                    nickname= {requestItem.nickname}
                    es_date= {requestItem.es_date}
                    start_time= {requestItem.start_time}
                    departure= {requestItem.departure}
                    destination={requestItem.destination}
                    number={requestItem.number}
                    rikkouho_id={requestItem.rikkouho_id}
                    lan_1={requestItem.lan_1}
                    lan_2={requestItem.lan_2}
                    lan_3={requestItem.lan_3}
                    status= {requestItem.status}
                    status_2= {requestItem.status_2}
                    emergency={requestItem.emergency}
                    history={history}
                    myId={myId}
                    mynickname={mynickname}
                    mypic={mypic}
                    />
                ))}
                </>
            )}
            {request.length===0 &&
              <Typography variant="caption" display="block" className={classes.text}>
                
              </Typography>}

        </div>
    )
}

export default WaitingReq
