import React, {useState} from 'react'
import TitleOffline from './TitleOffline'
import { makeStyles } from '@material-ui/core/styles';
import { Backdrop, Button, Fade, Modal, FormControl, InputLabel, MenuItem, Select, TextField} from '@material-ui/core';
import ModalItem from "./common/ModalItem";
import { withRouter } from 'react-router-dom';
import { db } from "./firebase";
import firebase from "firebase/app";
import { auth } from "./firebase";

// style
const useStyles = makeStyles((theme) => ({
    box: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
        position: "relative",
      },
    margin:{
        marginBottom: 70,
    },
    area:{
      marginBottom:20,
    },
    input:{
      display: "block",
      minWidth: 150,
    },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  button:{
    display: "block",
    margin: "0 auto",
    marginTop: 10,
    marginBottom: 10,
  },
  }));

const Register = (props) => {
    const classes = useStyles();

    //
    const [lastname, setLastname] = useState(''); 
    const [firstname, setFirstname] = useState(''); 
    const [email, setEmail] = useState(''); 
    const [password, setPassword] = useState(''); 
    const [userflg, setUserflg] = useState(''); 
    const [nickname, setNickname] = useState(''); 
    // modal
    const [open, setOpen] = useState(false);

    const handleClose = () => {
        setOpen(false);
      };
    const handleOpen = (e) => {
        // preventDefault
        e.preventDefault();
        setOpen(true);
      };



//
const handleSubmit=()=>{
  if(String(password).length<6){
    alert("6");
    return false;
  }
  // db
  // authuser
  const register = async () => {
    try {
      // firebase[createUserWithEmailAndPassword]
      // email, password
      await auth.createUserWithEmailAndPassword(email, password);
    } catch (error) {
      // 
      alert(error.message);
    }};
  register();
  // dbusers
  const id =db.collection('users').doc().id;
  db.collection("users").doc(id).set({
    first_name: firstname,
    last_name: lastname,
    email: email,
    password: password,
    user_flg: userflg,
    created_at: firebase.firestore.FieldValue.serverTimestamp(),
  });
  // profilesnicknameuser_id
  db.collection("profiles").add({
    user_id: id,
    nickname: nickname,
    created_at: firebase.firestore.FieldValue.serverTimestamp(),
  });

  props.history.push({
    // app.js
    pathname: '/',
});
};      

    return (
        <div>
        <TitleOffline title="" />
        <div className={classes.margin}></div>
        <div className={classes.box}>
        <form onSubmit={handleOpen}>
          <div className={classes.area}>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                required
                value={lastname}
                onChange={e => setLastname(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </FormControl>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                required
                value={firstname}
                onChange={e => setFirstname(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </FormControl>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                type="email"
                required
                value={email}
                onChange={e => setEmail(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </FormControl>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label="6"
                type="password"
                required
                value={password}
                onChange={e => setPassword(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </FormControl>                                           
            <FormControl component="fieldset" className={classes.input}>
              <InputLabel shrink id="userFlg" required>
                
              </InputLabel>
              <Select
                labelId="userFlg"
                id="userFlg"
                value={userflg}
                onChange={e => setUserflg(e.target.value)}
                displayEmpty
              >
                <MenuItem value="">
                  <em>/</em>
                </MenuItem>
                <MenuItem value="0"></MenuItem>
                <MenuItem value="1"></MenuItem>
              </Select>
              </FormControl>
            <FormControl component="fieldset" >
            <TextField
                label=""
                value={nickname}
                onChange={e => setNickname(e.target.value)}
                required
                InputLabelProps={{
                  shrink: true,
                }}
                />
          </FormControl>
          </div>
              <Button variant="contained" color="primary" type="submit"></Button>
              </form>        
        </div>

        {/* modal */}
        <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              className={classes.modal}
              open={open}
              onClose={handleClose}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
              timeout: 500,
              }}
            >
        <Fade in={open}>
        <ModalItem
          title=""
          text=""
          buttonText=""
          handleClose={handleClose}
          onSubmit={handleSubmit}
          />
        </Fade>
        </Modal>


        </div>
    )
}

export default withRouter(Register)
