import React , {useState, useEffect} from 'react'
import Title from '../../common/Title'
import { makeStyles } from '@material-ui/core/styles';
import { Backdrop, Button, Fade, Modal, FormControl, FormControlLabel, InputLabel, MenuItem, Radio, RadioGroup, Select, TextField, Typography } from '@material-ui/core';
import ModalItem from "../../common/ModalItem";
import { db } from "../../firebase";
import firebase from "firebase/app";

// style
const useStyles = makeStyles((theme) => ({
    box: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
        position: "relative",
      },
    margin:{
        marginBottom: 70,
    },
    area:{
      marginBottom:20,
    },
    input:{
      display: "block",
      minWidth: 150,
    },
    select:{
      paddingLeft: 30,
      minWidth: 150,
    },
    labelarea:{
      backgroundColor: "#f5f5f5",
      width: "100%",
      lineHeight: "40px",
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  }));


const GuestRequest = ({myId, mynickname, mypic, history}) => {
    const classes = useStyles();
    // 
    const [esdate, setEsdate] = useState('');

    // 
    const [date, setDate] = useState('');
    // dbnewDate
    const [newDate, setNewDate] = useState('');

    // 
    const [time, setTime] = useState(''); 

    // disable
    const [disabled, setDisabled] = useState(false); 

    //
    const [number, setNumber] = useState(''); 

    // 
    const [language1, setLanguage1] = useState('');
    const [language2, setLanguage2] = useState('');
    const [language3, setLanguage3] = useState('');

    // 
    const [departure, setDeparture] = useState(''); 

    // 
    const [destination, setDestination] = useState(''); 

    // modal
    const [open, setOpen] = useState(false);

    const [user, setUser] = useState([{id: "",},]);
    useEffect(() => {
      const firebaseData = db
          .collection("users")
          .where("user_flg", "==", "1")
          .onSnapshot((snapshot) =>
              setUser(
                snapshot.docs.map((doc) => ({
                  // firebasedoc
                  // post
                  id: doc.id,
                }))
              )
            );
      //   
      return () => {
          firebaseData();
          };
      }, []);

// 
let now = new Date();            //
let year = now.getFullYear();    //
let m = ("0"+(now.getMonth() + 1)).slice(-2);    //2
let d =  ("0"+now.getDate()).slice(-2);  //2
let nowDate = year + "/" + m + "/" + d;    

  // 
  const handleDateChange=(e)=>{
    // -/
    var str = e.target.value;
    var newstr = str.replace(/-/g, "/");
    setDate(e.target.value);
    setNewDate(newstr);
  };

  // 
  const handleEsdate = (e) => {
    setEsdate(e.target.value);
    if(e.target.value === "" )
    {setDisabled(true)};
    if(e.target.value !== "" )
    {setDisabled(false)};
  };

  //
  const handleSubmit=()=>{
    // 
    // console.log(esdate, "");
    // console.log(newDate, "");
    // console.log(time, "");
    // console.log(number, "");
    // console.log(language1, "1");
    // console.log(language2, "2");
    // console.log(language3, "3");
    // console.log(departure, "");
    // console.log(destination, "");
    const id =db.collection('escorts').doc().id;
  // db
  if(esdate===""){
    db.collection("escorts").doc(id).set({
      guest_id: myId,
      es_date: nowDate,
      start_time: "",
      departure: departure,
      destination: destination,
      number: number,
      lan_1: language1,
      lan_2: language2,
      lan_3: language3,
      status: "",
      emergency: 1,
      guest_nickname: mynickname,
      guest_pic: mypic,
      created_at: firebase.firestore.FieldValue.serverTimestamp(),
    });
    // volnotices
      user.map((item) => (
          db.collection("notices").add({
              escort_id: id,
              user_id: item.id,
              icon: 4,
              notice: `${mynickname}`,
              already_read: 0,
              created_at: firebase.firestore.FieldValue.serverTimestamp(),
            })
            ));
      }
  if(esdate===""){
    db.collection("escorts").doc(id).set({
      guest_id: myId,
      es_date: newDate,
      start_time: time,
      departure: departure,
      destination: destination,
      number: number,
      lan_1: language1,
      lan_2: language2,
      lan_3: language3,
      status: "",
      emergency: 0,
      guest_nickname: mynickname,
      guest_pic: mypic,
      created_at: firebase.firestore.FieldValue.serverTimestamp(),
    });
  }
  setEsdate("");
  setDate("");
  setNewDate("");
  setTime("");
  setNumber("");
  setDestination("");
  setDeparture("");
  setLanguage1("");
  setLanguage2("");
  setLanguage3("");
  setDisabled(false);
  setOpen(false);
  // 
  history.push({
    // detailid
    pathname: '/guest/detail',
    state: { escortId: id ,
             myId: myId,
             mynickname: mynickname,
             mypic: mypic}
  });
};

const handleClose = () => {
  setOpen(false);
};
const handleOpen = (e) => {
  // preventDefault
  e.preventDefault();
  setOpen(true);
};

    return (
        <>
        <Title title="" history={history}/>
        <div className={classes.margin}></div>
        <div className={classes.box}>
        <form onSubmit={handleOpen}>
          <div className={classes.area}>
          <Typography variant="subtitle1" display="block" className={classes.labelarea}>
           
          </Typography>
          <FormControl component="fieldset" className={classes.input}>
            <RadioGroup aria-label="time" name="time" value={esdate} onChange={handleEsdate}>
              <FormControlLabel value="" control={<Radio color="primary"/>} label=""/>
              <FormControlLabel value="" control={<Radio color="primary"/>} label="" />
            </RadioGroup>
          </FormControl>
          <FormControl component="fieldset" className={classes.select}>
            <TextField 
                type="date"
                label=""
                value={date}
                onChange={handleDateChange}
                required
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                disabled={disabled}
                />
              <TextField 
                type="time"
                label=""
                value={time}
                required
                margin="normal"
                onChange={e => setTime(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={{
                  step: 300, // 5 min
                }}
                disabled={disabled}
              />
          </FormControl>
          </div>

          <div className={classes.area}>
          <Typography variant="subtitle1" display="block" className={classes.labelarea}>
            
          </Typography>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                type="number"
                required
                value={number}
                onChange={e => setNumber(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </FormControl>
            <FormControl component="fieldset" className={classes.input}>
              <InputLabel shrink id="language" required>
                1
              </InputLabel>
              <Select
                labelId="language1"
                id="language1"
                value={language1}
                onChange={e => setLanguage1(e.target.value)}
                displayEmpty
              >
                <MenuItem value="">
                  <em></em>
                </MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
              </FormControl>
              <FormControl component="fieldset" className={classes.input}>
              <InputLabel shrink id="language2">
                2
              </InputLabel>
              <Select
                labelId="language2"
                id="language2"
                value={language2}
                onChange={e => setLanguage2(e.target.value)}
                displayEmpty
              >
                <MenuItem value="">
                  <em></em>
                </MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
              </FormControl>
              <FormControl component="fieldset" className={classes.input}>
              <InputLabel shrink id="language3">
                3
              </InputLabel>
              <Select
                labelId="language3"
                id="language3"
                value={language3}
                onChange={e => setLanguage3(e.target.value)}
                displayEmpty
              >
                <MenuItem value="">
                  <em></em>
                </MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
            </FormControl>
          </div>

          <div className={classes.area}>
            <Typography variant="subtitle1" display="block" className={classes.labelarea}>
            
            </Typography>
            <FormControl component="fieldset" >
            <TextField
                label=""
                value={departure}
                onChange={e => setDeparture(e.target.value)}
                required
                InputLabelProps={{
                  shrink: true,
                }}
                />
          </FormControl>
          </div>

          <div className={classes.area}>
            <Typography variant="subtitle1" display="block" className={classes.labelarea}>
            
            </Typography>
            <TextField
                label=""
                value={destination}
                onChange={e => setDestination(e.target.value)}
                required
                InputLabelProps={{
                  shrink: true,
                }}
                />
          </div>

              <Button variant="contained" color="primary" className={classes.button} type="submit"></Button>
              </form>        
        </div>

        {/* modal */}
        <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              className={classes.modal}
              open={open}
              onClose={handleClose}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
              timeout: 500,
              }}
            >
        <Fade in={open}>
        <ModalItem
          title=""
          text=""
          buttonText=""
          handleClose={handleClose}
          onSubmit={handleSubmit}
          />
        </Fade>
        </Modal>
        </>

    )
}

export default GuestRequest
