import React from 'react'
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Img from "../../img/noimage.jpg";
import { makeStyles } from '@material-ui/core/styles';
import { Avatar, Button, ListItem, ListItemAvatar, ListItemText } from '@material-ui/core';
import { withRouter } from 'react-router-dom';
import "../../style.css";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        marginBottom: 8,
      },
      paper: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
      },
      large: {
        width: theme.spacing(8),
        height: theme.spacing(8),
      },
      avatar:{
        paddingRight:"10px",
      },
      list:{
        flexDirection: "column",
        padding: "0 0 0 0",
      },
      list2:{
        padding: "0 0 0 0",
        position:"relative",
        marginBottom: "0",
      },
      button:{
        boxShadow: "none",
        position: "absolute",
        right: "0",
        bottom: "0",
        pointerEvents: "none",
    },
      image: {
        width: "5rem",
        height: "5rem",
      },
      img: {
        margin: 'auto',
        display: 'block',
        width: '100%',
        height: '100%',
        objectFit: "cover",
      },
  }));

  const RequestItem = ({myId, mynickname, mypic, history, id, volunteer_id, picture, nickname, es_date, start_time, departure, destination, number, lan_1, lan_2, lan_3, status, status_2, rikkouho_id, emergency}) => {
    const classes = useStyles();

    // click
    const handleClick=()=>{
      history.push({
        // detailid
        pathname: '/volunteer/detail',
        state: { escortId: id,
                 myId: myId,
                 mynickname: mynickname,
                 mypic: mypic,
                }
      });
    }

    // rikkouho_id
    let rikkouho ="";
      if (rikkouho_id) {
         rikkouho = rikkouho_id.includes(myId);
     }if(!rikkouho_id){
          rikkouho = false;
    };
  
    // volunteer_idmyId
      let disabled = null;
      if (status==="" && volunteer_id === myId) {
         disabled = false;
     }if(status==="" && volunteer_id !== myId){
         disabled = true;
    };
    

    return (
        <>
        <div key={id} className={classes.root}>
  
            {/*  */}
            <Paper className={classes.paper}>
            <ListItem button className={classes.list} onClick={handleClick} >
              <ListItem className={classes.list2}>
                <ListItemAvatar className={classes.avatar}>
                { picture ? (
                <>
                    <Avatar variant="rounded" src={picture} alt={`${nickname}`}  className={classes.large}/>
              </>
                ) : (
                <>
                    <Avatar variant="rounded" src={Img} alt=""  className={classes.large}/>
                </>
                )}
                </ListItemAvatar>
                <ListItemText 
                    className={classes.text}
                    primary={`${nickname}`}
                    secondary={
                        <>
                        <Typography component="span" variant="body2" display="block">
                        {es_date}  {start_time}
                        </Typography>
                        </>
                    }/>
              </ListItem>
              <ListItem  className={classes.list2}>
              <ListItemText
              secondary={
                <>
                  <Typography component="span" variant="body2" display="block">
                  {departure} 
                  </Typography>
                  <Typography component="span" variant="body2" display="block">
                  {destination}
                  </Typography>
                  <Typography component="span" variant="body2" display="block">
                  {lan_1}{ lan_2 && `${lan_2}`}{ lan_3 && `${lan_3}`}
                  </Typography>
                  <Typography component="span" variant="body2" display="block">
                  {number}
                  </Typography>
                </>
              }/>
              </ListItem>
              {/*  */}
              { status==="" && rikkouho=== false &&
                <>
                  <Button variant="contained" color="primary" className={classes.button} size="small">
                      {status}
                  </Button>
                </>
              }
              {/*  */}
              { status==="" && rikkouho=== true &&
                <>
                  <Button variant="contained" color="primary" className={classes.button} size="small">
                      
                  </Button>
                </>
              } 
              {/*  */}
              {status==="" && status_2==="" &&
                <>
                <Button variant="outlined" color="primary" className={classes.button} size="small">
                {status_2}
                </Button>
                </>
              }
              {/*  */}
              { status==="" && status_2==="" && disabled===false &&
                <>
                <Button variant="contained" color="secondary" className={classes.button} size="small">
                {status_2}
                </Button>
                </>
              }
              { status==="" && status_2==="" && disabled===false &&
                <>
                <Button variant="contained" color="secondary" className={classes.button} size="small">
                 
                </Button>
                </>
              }
              { status==="" && status_2==="" && disabled===false &&
                <>
                <Button variant="contained" color="secondary" className={classes.button} size="small">
                 
                </Button>
                </>
              }
              {/*  */}
              { status==="" && status_2!=="" && disabled===true && 
                <>
                <Button variant="outlined" color="primary" className={classes.button} size="small">
                 {status}
                </Button>
                </>
              }                       

            </ListItem>
            </Paper>
        </div>
        </>
    )
  }

export default withRouter(RequestItem)
