import React, {useState, useEffect} from 'react'
import Title from '../../common/Title'
import VolunteerInfoItem from './VolunteerInfoItem'
import Review from '../../volunteer/profile/Review'
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { db } from "../../firebase";

// style
const useStyles = makeStyles((theme) => ({
    reviewarea:{
        backgroundColor: "#f5f5f5",
        width: "100%",
    },
    box: {
      padding: theme.spacing(1),
      margin: 'auto',
      maxWidth: 500,
    },
    text:{
      margin: "0 auto",
      maxWidth: "500px",
      marginBottom: 10,
      textAlign: "center",
    },
  }));

const VolunteerInfo = (props) => {
    const classes = useStyles();
    // volunteer_id
    const volunteerId = props.location.state.volunteerId;

        // firebaseuseState
        const [profile, setProfile] = useState([
          {
              id: "",
              user_id: "",
              nickname:"",
              user_flg: "",
              picture: "",
              description: "",
              accout: "",
              created_at: null,
              updated_at: null,
          },
          ]);

    // firebasefirebase
    useEffect(() => {
      const firebaseData = db
          .collection("profiles")
          .where("user_id", "==", volunteerId)
          .onSnapshot((snapshot) =>
              setProfile(
                snapshot.docs.map((doc) => ({
                  // firebasedoc
                  // post
                  id: doc.id,
                  user_id: doc.data().user_id,
                  nickname: doc.data().nickname,
                  user_flg: doc.data().user_flg,
                  picture: doc.data().picture,
                  description: doc.data().description,
                  accout: doc.data().accout,
                  created_at: doc.data().created_at,
                  updated_at: doc.data().updated_at,
                }))
              )
            );
      //   
      return () => {
          firebaseData();
          };
      }, []);

    return (
        <div>
            <Title title="" history={props.history}/>
            {/* guestinfoitem */}
            {profile.length !== 0 && 
            profile.map((item) => (
            // GuestInfoItem
            <VolunteerInfoItem
            key={item.id}
            id={item.id}
            nickname={item.nickname}
            picture={item.picture}
            account={item.account}
            description={item.description}
            created_at={item.created_at}
            updated_at={item.updated_at}
            />
            ))}
        {profile.length===0 &&
              <Typography variant="caption" display="block" className={classes.text}>
                
              </Typography>}               
          <div className={classes.reviewarea}>
          <div className={classes.box}>
            <Typography variant="subtitle1" display="block">
            
            </Typography>
            <Review
            myId={volunteerId}
            /> 
          </div>
        </div>
        </div>
    )
}

export default VolunteerInfo
