import React,{useState, useEffect} from "react";
import { withRouter } from 'react-router-dom';
import TitleOffline from './TitleOffline'
import { makeStyles } from '@material-ui/core/styles';
import { Button, FormControl, TextField} from '@material-ui/core';
import { auth } from "./firebase";


// style
const useStyles = makeStyles((theme) => ({
  margin:{
      marginBottom: 150,
  },
  button:{
    display: "block",
    margin: "0 auto",
    marginTop: 20,
    marginBottom: 10,
  },
  text:{
    textAlign: "center",
  },
  input:{
    display:"block",
    width: 260,
    margin: "0 auto",
    textAlign: "center",
  },
  link:{
    display: "block",
    width: 65,
    margin: "0 auto",
    marginTop: 20,
    textDecoration: "underline",
    cursor: "pointer",
  },
}));

const App = (props) => {
  const classes = useStyles();
  const [email, setEmail] = useState(''); 
  const [password, setPassword] = useState(''); 

  useEffect(() => {
    // firebase
    // onAuthStateChanged
    // user
    // user
    const unSub = auth.onAuthStateChanged((user) => {
      // App(user)
      !user && props.history.push("/");
    });
    return () => unSub();
  }, [props.history]);

    // 
    const gotoRegister=()=>{
        props.history.push({
            pathname: '/register',
        })
    }

  return (
    <>
      <TitleOffline title=""/>
      <div className={classes.margin}></div>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                type="email"
                required
                value={email}
                onChange={e => setEmail(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
               
              />
            </FormControl><br/>
            <FormControl component="fieldset" className={classes.input}>
              <TextField
                label=""
                type="password"
                required
                value={password}
                onChange={e => setPassword(e.target.value)}
                InputLabelProps={{
                  shrink: true,
                }}
                
              />
            </FormControl>   
            <Button 
              variant="contained" 
              color="primary" 
              className={classes.button}
              onClick={
                async () => {
                  try{
                    await auth.signInWithEmailAndPassword(email, password);
                    props.history.push({
                      pathname: "/userflg",
                      state: { email: email,}
                    });
                  }catch(error){
                    // 
                    alert(error.message);
                  }
                }
              }>
              </Button>

        <p className={classes.link} onClick={gotoRegister}></p>
    </>
  );
};

export default withRouter(App);
