import React, {useState, useEffect} from 'react'
import Typography from '@material-ui/core/Typography';
import Img from "../../img/noimage.jpg";
import { makeStyles } from '@material-ui/core/styles';
import AddAPhotoIcon from "@material-ui/icons/AddAPhoto";
import { Button,  FormControl, TextField} from "@material-ui/core";
import { storage, db } from "../../firebase";
import firebase from "firebase/app";

// style
const useStyles = makeStyles((theme) => ({
    box: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
        position: "relative",
      },
    revise:{
        position: "absolute",
        top: 7,
        right: 5,
    },
    image: {
        width: "8rem",
        height: "8rem",
        marginBottom: "10px",
        backgroundImage: `url(${Img})`,
        backgroundSize: "cover",
        cursor: "pointer",
        position: "relative",
        verticalAlign: "middle",
        textAlign: "center",
        display: "table-cell",
        '&:hover': {backgroundColor:'rgba(128,128,128,0.3)'},
      },
    img: {
        margin: 'auto',
        display: 'block',
        width: '100%',
        height: '100%',
        objectFit: "cover",
      },
    inputFile: {
        display: "none",
      },
    imgIcon:{
        position: "absolute",
        left: 50,
        top: 52,
        color: "white",
      },
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
    margin:{
        marginBottom: 70,
    },
    reviewarea:{
        backgroundColor: "#f5f5f5",
        width: "100%",
    },
    update:{
        textAlign: "right",
    },
    account:{
        minWidth: 300,
    },
    description:{
        minWidth: "100%",
    },
  }));

const ProfileItem = ({id, user_id, nickname, picture, account, description, updated_at, created_at }) => {
    const classes = useStyles();

    const [request, setRequest] = useState([
      {
        id: "",
      },
      ]);

// 
useEffect(() => {
  const firebaseData = db
      .collection("escorts")
      .where("volunteer_id", "==", user_id)
      .onSnapshot((snapshot) =>
          setRequest(
            snapshot.docs.map((doc) => ({
              // firebasedoc
              id: doc.id,
            }))
          )
        );
  //   
  return () => {
      firebaseData();
      };
  }, []);

  if(typeof account === "undefined"){
    account=" ";
  }
  if(typeof description === "undefined"){
    description=" ";
  }
  if(typeof nickname === "undefined"){
    nickname=" ";
  }
    // 
    const [newpicture, setNewpicture] = useState(null);
    const [newnickname, setNewnickname] = useState(nickname);
    const [newaccount, setNewaccount] = useState(account);
    const [newdescription, setNewdescription] = useState(description);
    // disable
    const [disabled, setDisabled] = useState(true); 
    const onDisabled = () => {
        setDisabled(false);
      };
    const offDisabled = () => {
        setDisabled(true);
        setNewpicture(null);
        setNewnickname(nickname);
        setNewaccount(account);
        setNewdescription(description);
      };
    
    // 
    const onChangeImageHandler = (e) => {
        if (e.target.files[0]) {
          setNewpicture(e.target.files[0]);
          // 
          const reader = new FileReader();
          reader.onload = function(e) {
            document.getElementById('thumbnail').setAttribute('src', e.target.result);
          };
          reader.readAsDataURL(e.target.files[0]);
        }
          e.target.value = "";        
      };
    
    // 
    const handleSubmit=(e) =>{
        e.preventDefault();
        // 
        if(newpicture){
          // firebase
          // 
          const S =
              "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789"; //62
          const N = 16; //16 
          const randomMoji = Array.from(crypto.getRandomValues(new Uint32Array(N))) //0
              .map((n) => S[n % S.length])
              .join("");
          const fileName = randomMoji + "_" + newpicture.name;
  
          // firebase storage
          const uploadImg = storage.ref(`pictures/${fileName}`).put(newpicture);
          
          // firebase db
          uploadImg.on(
              firebase.storage.TaskEvent.STATE_CHANGED,
              () => {},  //
              (err) => {  //
                alert(err.message);
              },
              async () => { //
                await storage
                  .ref("pictures")
                  .child(fileName)
                  // storageURL
                  .getDownloadURL()
                  .then(async (url) => {
                    await db.collection("profiles").doc(id).update({
                      nickname: newnickname,
                      description:newdescription,
                      picture:url,
                      account:newaccount,
                      updated_at:firebase.firestore.FieldValue.serverTimestamp(),
                    });
                    // user_idescortsvolunteer_idvolunteer_picvolunteer_nickname
                    if (request.length!==0){
                    request.map((item) => (
                        db.collection("escorts").doc(item.id).update({
                          volunteer_nickname: newnickname,
                          volunteer_pic:url,
                          })
                          ));
                    } 
                  });
              }
            );
          } else {
              // 
        db.collection("profiles").doc(id).update({
          nickname: newnickname,
          description:newdescription,
          account:newaccount,
          updated_at:firebase.firestore.FieldValue.serverTimestamp(),
        });
        // user_idescortsvolunteer_idvolunteer_nickname
        if(request.length!==0){request.map((item) => (
          db.collection("escorts").doc(item.id).update({
            volunteer_nickname: newnickname,
            })
            ));
          }
      }
        setDisabled(true);
        setNewpicture(null);
        setNewnickname(newnickname);
        setNewaccount(newaccount);
        setNewdescription(newdescription);
    };

    
    return (
        <div key={id}>
        <div className={classes.margin}></div>
        <div className={classes.box}>
        {disabled===true &&
            <Button variant="contained" color="primary" className={classes.revise} onClick={onDisabled}>
                
            </Button>}
        {disabled===false &&
            <Button variant="outlined" color="primary" className={classes.revise} onClick={offDisabled}>
                
            </Button>}
        <form onSubmit={handleSubmit}>
            <label>
            <div className={classes.image}>
                {/*  */}
                { newpicture &&
                    <img id="thumbnail" src="" alt="" className={classes.img} />
                } 
                {/*  */} 
                {!newpicture && picture &&  
                <img id="thumbnail" src={picture} alt="" className={classes.img}/>}
                {disabled===false &&
                    <AddAPhotoIcon className={classes.imgIcon}/>  }
                    {/* type=file */}
                    <input
                    type="file"
                    onChange={onChangeImageHandler}
                    className={classes.inputFile}
                    disabled={disabled}
                    /> 
            </div>
            </label>

            <Typography variant="subtitle1" display="block">
            
            </Typography>
            <FormControl >
                  <TextField
                    value={newnickname}
                    onChange={(e) => setNewnickname(e.target.value)}
                    disabled={disabled}
                    required
                  />
            </FormControl>
            <Typography variant="subtitle1" display="block">
            SNS
            </Typography>
            <FormControl className={classes.account}>
                  <TextField
                    value={newaccount}
                    onChange={(e) => setNewaccount(e.target.value)}
                    disabled={disabled}
                  />
            </FormControl>

            <Typography variant="subtitle1" display="block">
                
            </Typography>
            <FormControl className={classes.description}>
                    <TextField
                        value={newdescription}
                        multiline
                        rows={5}
                        variant="outlined"
                        margin="normal"
                        //  
                        onChange={(e) => setNewdescription(e.target.value)}
                        disabled={disabled}
                    />
                    </FormControl>

        <Typography variant="body2" display="block" className={classes.update}>
        {updated_at &&
         <>{new Date(updated_at?.toDate()).toLocaleString()}</>}
        {!updated_at &&
         <>{new Date(created_at?.toDate()).toLocaleString()}</>}
        </Typography>
        <Button variant="contained" color="primary" disabled={disabled} type="submit"></Button>

        </form>
        </div>
        </div>
    )
}

export default ProfileItem
