import React, {useState} from 'react'
import Typography from '@material-ui/core/Typography';
import Img from "../../img/noimage.jpg";
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import Map from "../../volunteer/detail/Map";
import { withRouter } from 'react-router-dom';
import Done from "./Done";
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemText from '@material-ui/core/ListItemText';
import { withStyles } from '@material-ui/core/styles';
import { db } from "../../firebase";
import firebase from "firebase/app";

// style
const useStyles = makeStyles((theme) => ({
    box: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
        position: "relative",
      },
    button:{
        position: "absolute",
        top: 0,
        right: 5,
    },
    image: {
        width: "6rem",
        height: "6rem",
      },
    img: {
        margin: 'auto',
        display: 'block',
        width: '100%',
        height: '100%',
        objectFit: "cover",
      },
    volList:{
        display: "flex",
    },
    volInfo:{
        paddingLeft: "10px",
    },
    vol:{
        paddingTop: 10,
        paddingBottom: 10,
    },
    button2:{
        marginLeft: 10,
    },
    margin:{
        marginBottom: 70,
    },
    margin2:{
        marginBottom: 10,
    },
  }));


//  
const StyledMenu = withStyles({
    paper: {
      border: '1px solid #d3d4d5',
    },
  })((props) => (
    <Menu
      elevation={0}
      getContentAnchorEl={null}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'center',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'center',
      }}
      {...props}
    />
  ));
  const StyledMenuItem = withStyles((theme) => ({
    root: {
      '&:focus': {
        backgroundColor: theme.palette.primary.main,
        '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
          color: theme.palette.common.white,
        },
      },
    },
  }))(MenuItem);

const GuestDetailItem = ({
    id, picture, nickname, volunteer_id, rikkouho_id, es_date, start_time, 
    departure, departure_lat, departure_lon, destination, destination_lat, destination_lon,               
    number, lan_1, lan_2, lan_3, status, status_2, emergency, history, myId, mynickname, mypic}) => {

    const classes = useStyles();
    const [anchorEl, setAnchorEl] = useState(null);

    // menu
    const handleClick = (e) => {
      setAnchorEl(e.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
      };
    
    //
    const reviseRequest=()=>{
    // statusstatus2volunteer_idnickname,pic,rikkouho_id
        db.collection("escorts").doc(id).update({
            status: "",
            status_2: "",
            volunteer_id: "",
            volunteer_pic:"",
            volunteer_nickname:"",
            rikkouho_id: "",
          });
        // notice
        if(status===""){
        db.collection("notices").add({
            escort_id: id,
            user_id: volunteer_id,
            icon: 1,
            notice:`${mynickname}`,
            already_read: 0,
            created_at: firebase.firestore.FieldValue.serverTimestamp(),
          });
        };
    }

    //
    const canselRequest=()=>{
        // escortsid
        db.collection("escorts").doc(id).update({
            status: "",
            status_2: "",
            volunteer_id: "",
            volunteer_pic: "",
            volunteer_nickname: "",
          });
         // notice
        //  
        if(status===""){
          db.collection("notices").add({
            escort_id: id,
            user_id: volunteer_id,
            icon: 1,
            notice:`${mynickname}`,
            already_read: 0,
            created_at: firebase.firestore.FieldValue.serverTimestamp(),
          });
        };
        // 
        if(status==="" && rikkouho_id.length !== 0){
           // rikkouho_idid 
          rikkouho_id.map((item) => (
          db.collection("notices").add({
            escort_id: id,
            user_id: item,
            icon: 1,
            notice:`${mynickname}`,
            already_read: 0,
            created_at: firebase.firestore.FieldValue.serverTimestamp(),
          })
          ));
        };
    }
    
    //
    const gotoVol=()=>{
        history.push({
            // id
            pathname: '/guest/volunteerprofile',
            state: { volunteerId: volunteer_id, }
        });
    }
    //
    const gotoSelectVol=()=>{
        history.push({
            // id
            pathname: '/guest/volunteerselect',
            state: { rikkouho_id: rikkouho_id, 
                     escortId: id,
                     mynickname: mynickname,
                     mypic: mypic,
                     myId: myId,
            }
        });
    }

    // 
    const gotoForum=()=>{
        history.push({
            //forum
            pathname: '/forum',
            state: {escortId: id,
                    myId: myId,
                    mynickname: mynickname,
                    mypic: mypic,
                    }
        })
    }

    //
    const gotoChat=()=>{
        history.push({
            //forum
            pathname: '/chat',
            state: {escortId: id,
                    myId: myId,
                    mynickname: mynickname,
                    mypic: mypic,
                    }
        })
    }
// 
let now = new Date();            //
let year = now.getFullYear();    //
let m = ("0"+(now.getMonth() + 1)).slice(-2);    //2
let d =  ("0"+now.getDate()).slice(-2);  //2
let nowDate = year + m + d;   
// es_datenewDate
let esDate="";
if(es_date){
esDate = es_date.replace("/", "");
esDate = esDate.replace("/", "");
}
// nowDateesDate=true
const canselJudge = nowDate>esDate;

    return (
        <div key={id}>
        <div className={classes.margin}></div>

        { status_2!== "" && status_2 !== "" && status_2 !== "" && canselJudge===true && 
        <Typography variant="caption" display="block" color="primary">
        
        </Typography>
        }  

        <div className={classes.box}>           

            { status==="" &&
                <>
                  <Button variant="contained" color="primary" className={classes.button} size="large" endIcon={<ArrowDropDownIcon/>} onClick={handleClick}>
                      {status}
                  </Button>
                </>
            }
            { status=== "" && status_2!=="" &&
                <>
                <Button variant="outlined" color="primary" className={classes.button} size="large" endIcon={<ArrowDropDownIcon/>} onClick={handleClick}>
                    {status}
                </Button>
                </>
            }
            { status_2==="" &&
              <Button variant="outlined" color="primary" className={classes.button} size="large" >
                  {status_2}
              </Button>
            }
            { status==="" &&
              <Button variant="outlined" color="inherit" className={classes.button} size="large" >
                
              </Button>
            }            
            <StyledMenu
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
                >
                  {status=== "" &&
                <StyledMenuItem onClick={reviseRequest}>
                <ListItemText primary="" />
                </StyledMenuItem>
                }
                <StyledMenuItem onClick={canselRequest}>
                <ListItemText primary="" />
                </StyledMenuItem>
            </StyledMenu>

            <Typography variant="subtitle1" display="block">
            
            </Typography>
            <Typography variant="subtitle2" display="block">
            {es_date}  {start_time}
            </Typography>
            <Typography variant="body2" display="block">
            {lan_1}{ lan_2 && `${lan_2}`}{ lan_3 && `${lan_3}`}
            </Typography>
            <Typography variant="body2" display="block">
            {number}
            </Typography>
            <Typography variant="subtitle1" display="block">
            
            </Typography>
            <Typography variant="body2" display="block">
            {departure} 
            </Typography>
            <Typography variant="body2" display="block">
            {destination}
            </Typography>

            {/* map */}
            <Map
                departure_lat={departure_lat}
                departure_lon={departure_lon}
                destination_lat={destination_lat}
                destination_lon={destination_lon}
            />

            <div className={classes.vol}>
            {/*  */}
            { status==="" && rikkouho_id &&
            <>
                <Button variant="contained" onClick={gotoSelectVol}></Button>
                <Button variant="contained" color="primary" size="small" onClick={gotoForum} className={classes.button2}>
                    
                </Button>
                <Typography variant="caption" display="block">
                1
                </Typography>              
            </>
            }
            { status==="" &&  !rikkouho_id &&
            <>
                <Button variant="contained" color="primary" size="small" onClick={gotoForum} className={classes.button2}>
                    
                </Button>
                <Typography variant="caption" display="block">
                
                </Typography>             
            </>
            }
             {/*  */}
             { status==="" && 
                <>
                <Typography variant="subtitle1" display="block">
                    
                </Typography>
                <div className={classes.volList}>
                    <div>
                    { picture ? (
                        <>
                        <div className={classes.image}>
                            <img src={picture} alt={`${nickname}`} className={classes.img}/>
                        </div>
                        </>
                        ) : (
                        <>
                        <div className={classes.image}>
                            <img src={Img} alt="" className={classes.img}/>
                        </div>
                        </>
                        )
                    }
                    </div>
                    <div className={classes.volInfo}>
                    <Typography variant="body2" display="block">
                        {nickname}
                    </Typography>
                    <div className={classes.margin2}></div>
                    <Button variant="outlined" color="primary" size="small" onClick={gotoVol}>
                        
                    </Button>
                    <Button variant="contained" color="primary" size="small" onClick={gotoChat} className={classes.button2}>
                        
                    </Button>
                    </div>
                </div>                
                </>
            }
            </div>

            {/*  */}
            { status === "" && status_2 !== "" && status_2 !== "" &&
            <>
            <Done
                id={id} 
                volunteer_id={volunteer_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic}
                disabled="true"
                es_date={es_date}
                />
            <Typography variant="caption" display="block">
                
            </Typography>
            </>
            }
            {/*  */}
            { status === "" && status_2 === "" &&
            <Done
                id={id} 
                volunteer_id={volunteer_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic}
                es_date={es_date}
                />
            }
        { status_2 === "" &&
        <Typography variant="caption" display="block">
            
        </Typography>
        }
        { status === "" &&
        <Typography variant="caption" display="block" color="primary">
            
        </Typography>
        }        
        </div>

        </div>
    )
}

export default withRouter(GuestDetailItem)
