import React from 'react'
import Typography from '@material-ui/core/Typography';
import Img from "../../img/noimage.jpg";
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import Map from "./Map";
import { withRouter } from 'react-router-dom';
import Rikkouho from "./Rikkouho";
import RikkouhoCansel from "./RikkouhoCansel";
import Touchaku from "./Touchaku";
import Kanryou from "./Kanryou";

// detail
// style
const useStyles = makeStyles((theme) => ({
    box: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
        position: "relative",
      },
    button:{
        boxShadow: "none",
        pointerEvents: "none",
        position: "absolute",
        top: 0,
        right: 5,
    },
    image: {
        width: "6rem",
        height: "6rem",
      },
    img: {
        margin: 'auto',
        display: 'block',
        width: '100%',
        height: '100%',
        objectFit: "cover",
      },
    guestList:{
        display: "flex",
    },
    guestInfo:{
        paddingLeft: "10px",
    },
    guest:{
        paddingTop: 10,
        paddingBottom: 10,
    },
    button2:{
        marginLeft: 10,
    },
    margin:{
        marginBottom: 70,
    },
  }));

const DetailItem = ({
    id, picture, nickname, guest_id, volunteer_id, rikkouho_id, es_date, start_time,
    departure, departure_lat, departure_lon, destination, destination_lat, destination_lon,               
    number, lan_1, lan_2, lan_3, status, status_2, emergency, history, myId, mynickname, mypic}) => {

    const classes = useStyles();
    
    //
    const gotoGuest=()=>{
        history.push({
            // detailid
            pathname: '/volunteer/guestprofile',
            state: { guestId: guest_id,
                     escortId: id,
                     myId: myId,
                     mynickname: mynickname,
                     mypic: mypic, }
        });
    }

    // 
    const gotoForum=()=>{
        history.push({
            //forum
            pathname: '/forum',
            state: {escortId: id,
                    myId: myId,
                    mynickname: mynickname,
                    mypic: mypic,
                    }
        })
    }

    //
    const gotoChat=()=>{
        history.push({
            //forum
            pathname: '/chat',
            state: {escortId: id,
                    myId: myId,
                    mynickname: mynickname,
                    mypic: mypic,
                    }
        })
    }
    
    // rikkouho_id
    let rikkouho ="";
    if (rikkouho_id) {
        rikkouho = rikkouho_id.includes(myId);
    }if(!rikkouho_id){
        rikkouho = false;
    };

// 
let now = new Date();            //
let year = now.getFullYear();    //
let m = ("0"+(now.getMonth() + 1)).slice(-2);    //2
let d =  ("0"+now.getDate()).slice(-2);  //2
let nowDate = year + "/" + m + "/" + d;  
let judge ="";

let nowDate2 = year + m + d;   
// es_datenewDate2
let esDate="";
if(es_date){
esDate = es_date.replace("/", "");
esDate = esDate.replace("/", "");
}

console.log(esDate);
console.log(nowDate2);
// disabled
if (esDate > nowDate2) {
    // 
    judge = true;
}if (esDate <= nowDate2) {
    // 
    judge = false;
}

// nowDateesDate2=true
const canselJudge = nowDate2>esDate;

// volunteer_idmyId(false)
    let volunteerCheck = null;
    if (volunteer_id === myId) {
        volunteerCheck = true;
   }if(volunteer_id !== myId){
        volunteerCheck = false;
  };
    return (
        <div key={id}>
        <div className={classes.margin}></div>

        { status_2!== "" && status_2 !== "" && status_2 !== "" && canselJudge===true && volunteerCheck===true &&
        <Typography variant="caption" display="block" color="primary">
        
        </Typography>
        }
        { status_2!== "" && canselJudge===true && volunteerCheck===false &&
        <Typography variant="caption" display="block" color="primary">
        
        </Typography>
        }
        { volunteerCheck===false && status==="" &&
        <Typography variant="caption" display="block" color="primary">
        
        </Typography>
        }

        <div className={classes.box}>
            <Typography variant="subtitle1" className={classes.title}>
                {nickname}
            </Typography>
            { status==="" &&
                <>
                  <Button variant="contained" color="primary" className={classes.button} size="large">
                      {status}
                  </Button>
                </>
            }
            { status=== "" && status_2!=="" &&
                <>
                <Button variant="outlined" color="primary" className={classes.button} size="large">
                    {status}
                </Button>
                </>
            }
            { status_2==="" &&
              <Button variant="outlined" color="primary" className={classes.button} size="large" >
                  {status_2}
              </Button>
            }
            { status==="" &&
              <Button variant="outlined" color="inherit" className={classes.button} size="large" >
                
              </Button>
            } 

            <Typography variant="subtitle2" display="block">
            {es_date}  {start_time}
            </Typography>
             
            <Typography variant="subtitle2" display="block">
            
            </Typography>
            <Typography variant="body2" display="block">
            {departure} 
            </Typography>
            <Typography variant="body2" display="block">
            {destination}
            </Typography>

            {/* map */}
            <Map
                departure_lat={departure_lat}
                departure_lon={departure_lon}
                destination_lat={destination_lat}
                destination_lon={destination_lon}
            />
            <div className={classes.guest}>
            <Typography variant="subtitle1" display="block">
            
            </Typography>
            <div className={classes.guestList}>
            <div>
            { picture ? (
                <>
                <div className={classes.image}>
                    <img src={picture} alt={`${nickname}`} className={classes.img}/>
                </div>
                </>
                ) : (
                <>
                <div className={classes.image}>
                    <img src={Img} alt="" className={classes.img}/>
                </div>
                </>
                )
            }
            </div>
            <div className={classes.guestInfo}>
            <Typography variant="body2" display="block">
            {nickname}
            </Typography>
            <Typography variant="body2" display="block">
            {lan_1}{ lan_2 && `${lan_2}`}{ lan_3 && `${lan_3}`}
            </Typography>
            <Typography variant="body2" display="block">
            {number}
            </Typography>
            <Button variant="outlined" color="primary" size="small" onClick={gotoGuest}>
                
            </Button>
            {status === "" &&
            <Button variant="contained" color="primary" size="small" onClick={gotoForum} className={classes.button2}>
                
            </Button>
            }
            {status === "" && volunteerCheck===true &&
            <Button variant="contained" color="primary" size="small" onClick={gotoChat} className={classes.button2}>
                
            </Button>
            }
            </div>
            </div>
            </div>

            {/*  */}
            {/*  */}
            { status === "" && rikkouho === false && 
            <Rikkouho 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} 
                rikkouho_id={rikkouho_id}
                />
            }

            {/*  */}
            { status === "" && rikkouho === true && 
            <RikkouhoCansel 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic}
                rikkouho_id={rikkouho_id} />
            }

            {/*  disabled*/}
            { status === "" && status_2 === "" && judge===false && volunteerCheck===true &&
            <Touchaku 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} 
                />
            }
            {/*  */}
            { status === "" && status_2 === "" && judge===true && volunteerCheck===true &&
            <Touchaku 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} 
                disabled="true"
                />
            }

            {/*  */}
            { status === "" && status_2 === "" && volunteerCheck===true &&
            <Kanryou 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} />
            }
            {/*  */}
            { status === "" && status_2 === "" && volunteerCheck===true &&
            <Typography variant="caption" display="block">
                
            </Typography>
            }
            {/*  */}
            { status_2 === "" &&
            <Typography variant="caption" display="block">
                
            </Typography>
            }
            {/*  */}
            { status === "" &&
            <Typography variant="caption" display="block" color="primary">
                
            </Typography>
            }                
        </div>
        </div>
    )
}

export default withRouter(DetailItem)
